import React, { useRef, useState } from "react";
import { Col, Input, Row } from "reactstrap";

const FileInputComponent = (props) => {
  const { fieldName, defaultVal, fileType } = props;
  const [file, setFile] = useState(null);
  const [changeFile, setChangeFile] = useState(false);
  const fileInputRef = useRef(null);
  const [alertMsg, setAlertMsg] = useState(null);

  const handleFileChange = (event) => {
    let fileExt = event.target.files[0].name.split(".").slice(-1)[0];
    if (
      ["jpg", "jpeg", "png", "gif"].includes(fileExt) === false &&
      fileType === "image"
    ) {
      setAlertMsg("Please select an image!");
    } else {
      setAlertMsg(null);
      setFile(event.target.files[0]);
      setChangeFile(true);
      props.updateFileData(fieldName, event.target.files[0]);
    }
  };

  const handleUndoSelection = (event) => {
    setFile(null);
    setChangeFile(false);
    fileInputRef.value = null;
    props.updateFileData(fieldName, null);
    setAlertMsg(null);
  };

  const handleFileRemove = (event) => {
    setFile("No File");
    setChangeFile(true);
    setAlertMsg(null);
    fileInputRef.value = null;
    props.updateFileData(fieldName, "No File");
  };

  return (
    <>
      <Row>
        <Col className="col-12 col-md-6">
          <span className="px-3 text-capitalize">
            {fieldName
              ? fieldName
                  .replace(/([a-z])([A-Z])/g, "$1 $2")
                  .replace(/([A-Z])([A-Z][a-z])/g, "$1 $2")
              : "Text here"}
          </span>
        </Col>
        <Col className="col-12 col-md-6">
          <div
            className="position-relative mx-4 text-center mt-2"
            style={{ maxHeight: "150px" }}
          >
            {fileType === "image" ? (
              <img
                src={
                  file === null
                    ? defaultVal
                      ? `/uploads/${defaultVal}`
                      : `/images/no-image-banner.jpg`
                    : file === "No File"
                    ? `/images/no-image-banner.jpg`
                    : URL.createObjectURL(file)
                }
                alt="No Profile Pic Available"
                className="profile_pic mw-100 position-relative"
              />
            ) : (
              <div
                className="position-relative mw-100"
                style={{ height: "150px" }}
              >
                <a
                  href={`/uploadedFiles/${defaultVal}`}
                  alt={defaultVal}
                  style={{ maxHeight: "80px" }}
                >
                  Download Attachment
                </a>
              </div>
            )}

            <div className="position-absolute blank-background d-flex align-items-center justify-content-center">
              <div>
                {changeFile ? (
                  <span
                    className="sp-btn-secondary rounded-0 px-3 mx-1 mx-md-2 cursor-pointer"
                    onClick={handleUndoSelection}
                  >
                    Reset
                  </span>
                ) : (
                  <>
                    <span
                      className="sp-btn-secondary rounded-0 px-3 mx-1 mx-md-2 cursor-pointer"
                      onClick={handleFileRemove}
                    >
                      Remove
                    </span>
                    <span className="sp-btn-secondary rounded-0 px-3 mx-1 mx-md-2 position-relative cursor-pointer">
                      Change
                      <Input
                        type="file"
                        name={fieldName}
                        id={fieldName}
                        onChange={handleFileChange}
                        ref={fileInputRef}
                        style={{
                          position: "absolute",
                          opacity: 0,
                          top: 0,
                          left: 0,
                          right: 0,
                          bottom: 0,
                        }}
                        className="cursor-pointer"
                      />
                    </span>
                    {alertMsg && (
                      <div className="text-warning f-size-15 t-back2 mt-2">
                        <span className="t-back2 p-3">{alertMsg}</span>
                      </div>
                    )}
                  </>
                )}
              </div>
            </div>
          </div>
        </Col>
      </Row>
    </>
  );
};

export default FileInputComponent;
